import React from 'react'
import {
  Button,
  Form,
  Grid,
  Icon,
  Header,
  Message,
  Segment,
  Divider
} from 'semantic-ui-react'
import { Link } from 'react-router-dom'
import Notification from './Notification'

const RegistrationForm = ({
  onSubmit,
  handleChange,
  username,
  password,
  password2,
  message,
  color
}) => {
  return (
    <div className='login-form' style={{ overflow: 'hidden' }}>
      <Notification message={message} color={color} />
      <Divider hidden />
      <Divider hidden />
      {message ? (
        <div />
      ) : (
        <div>
          <Divider hidden />
          <Divider hidden />
          <Divider hidden />
          <Divider hidden />
          <Divider hidden />
          <Divider hidden />
          <Divider hidden />
        </div>
      )}
      <Grid
        textAlign='center'
        style={{ height: '100%' }}
        verticalAlign='middle'>
        <Grid.Column style={{ maxWidth: 450 }}>
          <Header as='h2' color='blue' textAlign='center'>
            <Icon name='user circle' /> Create new account
          </Header>
          <Form onSubmit={onSubmit} size='large'>
            <Segment>
              <Form.Input
                fluid
                icon='user'
                name='newUsername'
                iconPosition='left'
                placeholder='Username'
                value={username}
                onChange={handleChange}
              />
              <Form.Input
                fluid
                icon='lock'
                name='newPassword'
                iconPosition='left'
                placeholder='Password'
                type='password'
                value={password}
                onChange={handleChange}
              />
              <Form.Input
                fluid
                icon='lock'
                name='newPassword2'
                iconPosition='left'
                placeholder='Repeat password'
                type='password'
                value={password2}
                onChange={handleChange}
              />
              <Button color='blue' fluid size='large' type='submit'>
                Submit
              </Button>
            </Segment>
          </Form>
          <Message>
            <Link to='/login'>Login to account</Link>
          </Message>
        </Grid.Column>
      </Grid>
      <Divider hidden />
    </div>
  )
}
export default RegistrationForm
